import Cookies from "js-cookie";

// Question
const questionIndexKey = "wat_question_index";
export const getQuestionIndex = () => Cookies.get(questionIndexKey);
export const setQuestionIndex = (index: string) =>
  Cookies.set(questionIndexKey, index);

// User
const tokenKey = "wat_access_token";
export const getToken = () => Cookies.get(tokenKey);
export const setToken = (token: string) => Cookies.set(tokenKey, token);
export const removeToken = () => Cookies.remove(tokenKey);
